<template>
  <!--个人中心-->
  <div>
    <div v-if="isLoading" style="margin-top: 24%;margin-left: 45%;">
      <Header></Header>
      <a-spin size="large"/>
    </div>
    <div v-else style="margin-top: 10%;margin-left: 23%;">
      <Header></Header>
      <a-icon type="user"/>
      个人信息
      <div class="user-info">
        <ul>
          <li>
            头像：
            <a-avatar icon="user" :size="64"/>
            <a-button type="link" disabled>
              暂不支持修改头像
            </a-button>
          </li>
          <li>
            用户名： {{ userInfo.username }}
            <a-button type="link" disabled>
              暂不支持修改用户名
            </a-button>
          </li>
          <li>
            手机号： {{ userInfo.phone.substring(0, 3) }}****{{ userInfo.phone.substring(7) }}
            <a-button type="link">
              修改
            </a-button>
            <div style="margin-bottom: 2%;"></div>
          </li>
          <li>
            密码： ************
            <a-button type="link">
              修改
            </a-button>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
  import Header from "@/components/Header";
  import storage from "store";
  import request from "@/utils/request";

  export default {
    name: 'UserCenter',
    components: {Header},
    data() {
      return {
        userInfo: {
          uid: '',
          username: '',
          phone: ''
        },
        isLoading: true
      }
    },
    methods: {
      getUserInfo() {
        this.userInfo = storage.get('userInfo');
        this.isLoading = true;
        if (this.userInfo) {
          this.isLoading = false;
          return;
        }

        request({
          method: 'get',
          url: '/ouser'
        }).then(res => {
          if (res.code === 1) {
            const userInfo = res.data.map;
            this.userInfo = userInfo;
            storage.set('userInfo', userInfo);
          } else {
            this.$message.error(res.msg);
          }
        }).catch(error => {
          this.$message.error('服务器错误！');
          console.log(error);
        })
      }
    },
    computed: {
      username() {
        const userInfo = storage.get('userInfo');
        if (userInfo) {
          return userInfo.username;
        }

        this.getUserInfo();
        return storage.get('userInfo').username;
      }
    },
    mounted() {
      this.getUserInfo();
    },
    activated() {
      this.isLoading = false;
    }
  }
</script>

<style scoped>
  .user-info {
    margin-top: 1%;
    width: 60%;
    border: 2px solid lightgray;
    padding-left: 13%;
    padding-top: 6%;
    font-family: -apple-system, BlinkMacSystemFont, Segoe UI, PingFang SC, Hiragino Sans GB, Microsoft YaHei, Helvetica Neue, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol;
    font-variant: tabular-nums;
    font-feature-settings: "tnum";
    font-size: 16px;
  }

  .user-info li {
    list-style-type: none;
    margin-bottom: 5%;
  }
</style>